'use strict';


var links = document.querySelectorAll('a, button');
var mapContainer = document.querySelector('.sc-asdb-map-container');


function cancel(e) {
  e.preventDefault();
}


Array.prototype
  .filter.call(links, function(link) {
    return !mapContainer.contains(link);
  })
  .forEach(function(link) {
    link.classList.add('not-ready');
    link.addEventListener('click', cancel);
  });
